:root {
  font-size: 62.5%;
}

* {
  -webkit-touch-callout: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

body, html {
  background-color: #f90;
  background-image: -webkit-linear-gradient(315deg, #f90, #ff0052);
  background-image: -o-linear-gradient(315deg, #f90, #ff0052);
  background-image: linear-gradient(135deg, #f90, #ff0052);
  color: #fcfcfc;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  overflow: auto;
  text-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  text-align: center;
}

.logo {
  margin: 2rem;
  width: 90vw;
}

.word-card-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  text-transform: uppercase;
  width: 100vw;
}

.restart-button {
  cursor: pointer;
  margin-top: auto;
}

.word-card {
  background: none;
  border-radius: 3px;
  border: none;
  cursor: pointer;
  font-size: 5.4rem;
  margin: .2em;
  height: 1.6em;
  width: 1.6em;
}

@media (min-width: 38em) {
  .logo {
      max-width: 38em;
  }
}


